.jenic__header {
  display: flex;
}

.jenic__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin-left: 90px;
}

.jenic__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;

  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.jenic__header-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;

  color: var(--color-text);
  margin-top: 1.5rem;
}

.jenic__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.jenic__header-content__input input {
  flex: 2;
  width: 100%;
  min-height: 50px;
  background: var(--color-footer);
  border-radius: 5px 0px 0px 5px;
  font-family: var(--font-family);
  font-size: 20px;
  line-height: 27px;
  border: 2px solid var(--color-footer);
  padding: 0 1rem;
  outline: none;
  color: #fff;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.jenic__header-content__input button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  background: #ff4820;
  font-family: "Manrope";
  font-style: bold;
  font-size: 20px;
  line-height: 27px;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  color: #ffffff;
}

.jenic__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.jenic__header-content__people img {
  width: 181.79px;
  height: 38px;
}

.jenic__header-content__people p {
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #ffffff;
  text-align: center;
}

.jenic__header-image{
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
}

.jenic__header-image img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px){
    .jenic__header{
        flex-direction: column;
    }
    .jenic__header-content{
        margin: 0 0 3rem;
    }
}

@media screen and (max-width:650px){
    .jenic__header h1{
        font-size: 48px;
        line-height: 60px;
    }

    .jenic__header p{
        font-size: 16px;
        line-height: 24px;
    }
    .jenic__header-content__people{
        flex-direction: column;
    }
    .jenic__header-content__people p{
        margin: 0;
    }
    .jenic__header-content__input input,
    .jenic__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width:490px){
    .jenic__header h1{
        font-size: 36px;
        line-height: 48px;
    }

    .jenic__header p{
        font-size: 14px;
        line-height: 24px;
    }
    .jenic__header-content__input input,
    .jenic__header-content__input button{
        font-size: 12px;
        line-height: 16px;
    }

}